<template>
    <Layout :style="{minHeight: '100vh'}">
        <Header>
            <Menu mode="horizontal" theme="dark" active-name="1">
                <div class="layout-logo"></div>
                <div class="layout-nav">
                    <MenuItem name="1">
                        <Icon type="ios-navigate"></Icon>
                        Item 1
                    </MenuItem>
                    <MenuItem name="2">
                        <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '0 20px'}"
                                type="md-menu" size="24"></Icon>
                    </MenuItem>
                </div>
            </Menu>
        </Header>
        <Layout>
            <Sider ref="side1" hide-trigger :style="{background: '#fff'}" collapsible :collapsed-width="0"
                     v-model="isCollapsed">
                <Menu theme="light" width="auto" :open-names="['1']" style="height: 100%" @on-select="menuItemAction">
                    <Submenu :name="'m'+i" :key="i" v-for="(httpInfos,tag, i) in apiData.collection">
                        <template slot="title">
                            <Icon type="ios-book"></Icon>
                            {{tag}}
                        </template>
                        <!--TODO 对齐方式-->
                        <template v-for="httpInfo in httpInfos">
                            <MenuItem :name="httpInfo.index" :key="httpInfo.index">
                                <span v-if="httpInfo.method ==='GET'"
                                      style="font-size: 10px;font-weight:bold;color: #18BE6B;">{{httpInfo.method}}</span>
                                <span v-else-if="httpInfo.method ==='POST'"
                                      style="font-size: 10px;font-weight:bold;color: #FF9901;">{{httpInfo.method}}</span>
                                <span v-else-if="httpInfo.method ==='PUT'"
                                      style="font-size: 10px;font-weight:bold;color: #2D8CF0;">{{httpInfo.method}}</span>
                                <span v-else-if="httpInfo.method ==='DELETE'"
                                      style="font-size: 10px;font-weight:bold;color: #ED4114;">{{httpInfo.method}}</span>
                                <span v-else style="font-size: 10px;font-weight:bold;color: #FFF1F0;">{{httpInfo.method}}</span>
                                {{httpInfo.name}}
                            </MenuItem>
                        </template>
                    </Submenu>
                </Menu>
            </Sider>
            <Layout :style="{minHeight: '100vh'}">
                <Content :style="{padding: '24px', background: '#fff'}">

                    <Tabs>
                        <TabPane label="API 文档" class="row-padding-bottom">

                            <Row class="no-border">
                                <Col>
                                    <h2>接口说明</h2>
                                </Col>
                                <Col>
                                    <Table :columns="apiInfoColumns" :data="apiInfo" :show-header="false"></Table>
                                </Col>
                            </Row>

                            <!--参数信息-->
                            <Row>
                                <Col>
                                    <h2>请求参数</h2>
                                </Col>
                                <Col>
                                    <Table border :columns="paramsColumns" :data="rootParams.props"></Table>
                                </Col>
                            </Row>

                            <template v-for="sub of subParams">
                                <Row :key="sub.name">
                                    <Col>
                                        <h3>类型：{{sub.title}}</h3>
                                    </Col>
                                    <Col>
                                        <Table border :columns="objectColumns" :data="sub.props"></Table>
                                    </Col>
                                </Row>
                            </template>

                            <!--响应信息-->
                            <Row>
                                <Col>
                                    <h2>响应信息</h2>
                                </Col>
                                <Col>
                                    <Table border :columns="responsesColumns" :data="rootResponses.props"></Table>
                                </Col>
                            </Row>

                            <template v-for="sub of subResponses">

                                <Row :key="sub.name">
                                    <Col>
                                        <h3>类型：{{sub.title}}</h3>
                                    </Col>
                                    <Col>
                                        <Table border :columns="objectColumns" :data="sub.props"></Table>
                                    </Col>
                                </Row>
                            </template>

                        </TabPane>
                        <TabPane label="在线调试"></TabPane>
                    </Tabs>
                </Content>
            </Layout>
        </Layout>
    </Layout>
</template>
<script>
    import store from '@/store'
    import {findHttpInfo, findAllSchema, methodColumnRender} from '@/util/utils'

    export default {
        name: 'app',
        data() {
            return {
                isCollapsed: false,
                apiInfoColumns: [],
                paramsColumns: [
                    {title: '名称', key: 'name'},
                    {title: '描述', key: 'description'},
                    {title: '位置', key: 'in'},
                    {title: '必须', key: 'required'},
                    {title: '类型', key: 'type'},
                    {title: '格式', key: 'format'}],
                objectColumns: [
                    {title: '名称', key: 'name'},
                    {title: '描述', key: 'description'},
                    {title: '类型', key: 'type'},
                    {title: '格式', key: 'format'}],
                responsesColumns: [
                    {title: '状态', key: 'status'},
                    {title: '描述', key: 'description'},
                    {title: '类型', key: 'type'},
                    {title: '格式', key: 'format'}],
                apiInfo: [],
                httpInfo: {},
                rootParams: {},
                subParams: [],
                rootResponses: {},
                subResponses: []
            }
        },
        methods: {
            collapsedSider() {
                this.$refs.side1.toggleCollapse();
            },
            menuItemAction(index) {

                let httpInfo = findHttpInfo(store.state.apiData, index);
                this.$data.httpInfo = httpInfo;

                this.$data.apiInfoColumns = [
                    {
                        title: '',
                        key: 'k1',
                        width: 110,
                        align: 'right',
                        render: methodColumnRender,
                    }, {'title': '', 'key': 'k2'}];

                this.$data.apiInfo = [{
                    'k1': httpInfo.method,
                    'k2': httpInfo.path
                }, {
                    'k1': '请求体类型：',
                    'k2': httpInfo.consumes
                }, {
                    'k1': '响应体类型：',
                    'k2': httpInfo.produces
                }];

                this.$data.rootParams = httpInfo.params;
                const subParams = [];
                findAllSchema(httpInfo.params, store.state.apiData.definitions, subParams);
                this.$data.subParams = subParams;

                this.$data.rootResponses = httpInfo.responses;
                const subResponses = [];
                findAllSchema(httpInfo.responses, store.state.apiData.definitions, subResponses);
                this.$data.subResponses = subResponses

            }
        },
        computed: {
            apiData() {
                return store.state.apiData
            }
        }
    }
</script>
<style>
    html {
        height: 100%;
    }

    body {
        height: 100%;
    }

    .row-padding-bottom .ivu-row {
        padding-bottom: 50px;
    }

    .no-border .ivu-table-wrapper {
        border: 0 solid #dcdee2;
    }

    .no-border .ivu-table-wrapper td {
        border: 0 solid #e8eaec;
    }

    .no-border .ivu-table:after {
        width: 0;
    }

</style>
